import React, { useState } from 'react';
import axios from 'axios';
import '../css/ContactPage.css'; // Ensure this CSS file includes your custom styles

const ContactPage = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    message: ''
  });

  const handleChange = (event) => {
    setFormData({ ...formData, [event.target.name]: event.target.value });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    axios.post('/contact', formData)
      .then((response) => {
        alert('Message sent successfully!');
      })
      .catch((error) => {
        alert('Error sending message: ' + error.message);
      });
  };

  return (
    <div className="contact-page container mt-5">
      <div className="row mb-5">
        <div className="col-lg-6">
          <h2 className="text-center mb-4">Reach Out to Us</h2>
          <p className="text-center mb-4">
            For inquiries, donations, or collaborations, please reach out to us through the contact information below or fill out the form.
          </p>
          <div className="contact-info bg-light p-4 rounded shadow-sm">
            <h3 className="text-primary mb-4">Contact Information</h3>
            <ul className="list-unstyled">
              <li><i className="bi bi-telephone-fill"></i> <strong>Phone:</strong> <a href="tel:+919881373585">+91 9881373585</a></li>
              <li><i className="bi bi-envelope-fill"></i> <strong>Email:</strong> <a href="mailto:karnalact@gmail.com">karnalact@gmail.com</a>, <a href="mailto:chandrakantshahasane@gmail.com">chandrakantshahasane@gmail.com</a></li>
              <li><i className="bi bi-geo-alt-fill"></i> <strong>Address:</strong> Rahul Complex, D-69, Paud Road, Kothrud, Pune - 411038</li>
            </ul>
          </div>
        </div>
        <div className="col-lg-6">
          <h3 className="text-center mb-4">Our Location</h3>
          <div className="map-container rounded shadow-sm">
            <iframe 
              src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3769.471373867287!2d73.82652281496566!3d18.51006788723538!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bc2c1968e4c053d%3A0x8ed5ef9e4b3d02b0!2sRahul%20Complex%2C%20Paud%20Road%2C%20Kothrud%2C%20Pune%2C%20Maharashtra%20411038!5e0!3m2!1sen!2sin!4v1631112290906!5m2!1sen!2sin" 
              width="100%" 
              height="300" 
              style={{ border: 0 }} 
              allowFullScreen="" 
              loading="lazy"
              title="Google Map"
            ></iframe>
          </div>
        </div>
      </div>
      
      <h3 className="text-center mb-4">Send Us a Message</h3>
      <form onSubmit={handleSubmit}>
        <div className="form-row">
          <div className="form-group col-md-6">
            <label htmlFor="name">Name</label>
            <input 
              type="text" 
              className="form-control" 
              id="name" 
              name="name" 
              value={formData.name} 
              onChange={handleChange} 
              required 
            />
          </div>
          <div className="form-group col-md-6">
            <label htmlFor="email">Email</label>
            <input 
              type="email" 
              className="form-control" 
              id="email" 
              name="email" 
              value={formData.email} 
              onChange={handleChange} 
              required 
            />
          </div>
        </div>
        <div className="form-group">
          <label htmlFor="message">Message</label>
          <textarea 
            className="form-control" 
            id="message" 
            name="message" 
            rows="5" 
            value={formData.message} 
            onChange={handleChange} 
            required
          ></textarea>
        </div>
        <button type="submit" className="btn btn-primary btn-lg btn-block">Submit</button>
      </form>
    </div>
  );
};

export default ContactPage;