import React, { useState } from 'react';
import { useLocation } from 'react-router-dom';
import { Document, Page, pdfjs } from 'react-pdf';
import { Container, Button } from 'react-bootstrap';
import '../css/PdfViewer.css'; // Styling for PDF reader


// Set the worker source for pdfjs-dist
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;


const BookReader = () => {
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  const bookPath = `/Books/${params.get('book')}`; // Path relative to the public directory

  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);
  const [error, setError] = useState(null);

  function onDocumentLoadSuccess({ numPages }) {
    setNumPages(numPages);
  }

  function onDocumentLoadError(error) {
    console.error('Failed to load PDF:', error);
    setError('Failed to load PDF. Please try again later.');
  }

  return (
    <Container className="pdf-reader mt-5">
      <Button href="/publications" variant="primary" className="mb-3">Back to Publications</Button>
      {error ? (
        <p>{error}</p>
      ) : (
        <Document
          file={bookPath}
          onLoadSuccess={onDocumentLoadSuccess}
          onLoadError={onDocumentLoadError}
          className="pdf-document"
        >
          <Page pageNumber={pageNumber} />
        </Document>
      )}
      <div className="pdf-navigation">
        <Button
          variant="secondary"
          onClick={() => setPageNumber(prevPageNumber => Math.max(prevPageNumber - 1, 1))}
          disabled={pageNumber <= 1}
        >
          Previous
        </Button>
        <Button
          variant="secondary"
          onClick={() => setPageNumber(prevPageNumber => Math.min(prevPageNumber + 1, numPages))}
          disabled={pageNumber >= numPages}
        >
          Next
        </Button>
      </div>
    </Container>
  );
};

export default BookReader;
