import React from 'react';
import '../css/ProjectsPage.css'; // Make sure to create or update the CSS file for custom styles
import olakh from '../images/img1/olakh.jpg';
import yatra from '../images/img1/yatra.jpg';
import school from '../images/img1/school.jpg';

const ProjectsPage = () => {
  const projects = [
    {
      title: 'Deshbhakti Exhibition',
      description: 'Exhibiting photos of 2,000 freedom fighters and spreading awareness about their contributions.',
      imageUrl: olakh, // Actual image URL
      link: '#'
    },
    {
      title: '948-Day Yatra',
      description: 'Covering various regions of Maharashtra and Goa to showcase freedom fighters\' stories.',
      imageUrl: yatra, // Actual image URL
      link: '#'
    },
    {
      title: 'School Campaigns',
      description: 'Under the "Olkha Deshbhaktanchi - Shala Tethe Kranti Mandir" initiative, spreading patriotism among school and colleges.',
      imageUrl: school, // Actual image URL
      link: '#'
    }
  ];

  const upcomingProjects = [
    {
      title: 'Patriotic Songwriting and Singing Competition',
      description: 'A Patriotic Songwriting and Singing Competition invites students to create and perform songs, celebrating national pride and unity through the power of music.',
      imageUrl: 'https://via.placeholder.com/600x400', // Replace with actual image URL
      link: '#'
    },
    {
      title: 'Virtual Reality Freedom Fighters Experience',
      description: 'A cutting-edge VR exhibition lets visitors experience key moments from the lives of freedom fighters through immersive technology.',
      imageUrl: 'https://via.placeholder.com/600x400', // Replace with actual image URL
      link: '#'
    },
    {
      title: 'Heritage Walks Across Historical Sites',
      description: 'A series of guided heritage walks at key historical sites where freedom battles were fought, raising awareness about their significance..',
      imageUrl: 'https://via.placeholder.com/600x400', // Replace with actual image URL
      link: '#'
    },
    {
      title: 'Freedom Fighters Documentary Series',
      description: 'A documentary series highlighting the lesser-known heroes of Indias independence movement, featuring interviews with historians and archival footage.',
      imageUrl: 'https://via.placeholder.com/600x400', // Replace with actual image URL
      link: '#'
    },
    {
      title: 'Krantijyoti  The Torchbearers of Freedom (Women\'s Edition)',
      description: 'A project highlighting women freedom fighters with exhibitions, talks, and a book launch, celebrating their vital contributions to the struggle for independence.',
      imageUrl: 'https://via.placeholder.com/600x400', // Replace with actual image URL
      link: '#'
    },
    {
      title: 'Youth Patriotism Marathon',
      description: 'A marathon for youth, running through key historical routes, to commemorate the long journey toward freedom, emphasizing the importance of physical fitness and national pride.',
      imageUrl: 'https://via.placeholder.com/600x400', // Replace with actual image URL
      link: '#'
    },

  ];

  return (
    <div className="projects-page container mt-5">
      <h2 className="text-center mb-4">Projects by Karnala Charitable Trust</h2>
      <div className="row">
        {projects.map((project, index) => (
          <div key={index} className="col-md-4 mb-4">
            <div className="card project-card">
              <img src={project.imageUrl} alt={project.title} className="card-img-top" />
              <div className="card-body">
                <h5 className="card-title">{project.title}</h5>
                <p className="card-text">{project.description}</p>
                <a href={project.link} className="btn btn-primary">Learn More</a>
              </div>
            </div>
          </div>
        ))}
      </div>

      <h2 className="text-center mt-5 mb-4">Upcoming Projects</h2>
      <div className="row">
        {upcomingProjects.map((project, index) => (
          <div key={index} className="col-md-4 mb-4">
            <div className="card project-card">
              <img src={project.imageUrl} alt={project.title} className="card-img-top" />
              <div className="card-body">
                <h5 className="card-title">{project.title}</h5>
                <p className="card-text">{project.description}</p>
                <a href={project.link} className="btn btn-primary">Learn More</a>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ProjectsPage;
