import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import '../css/Header.css';

const Header = () => {
  const [isNavbarOpen, setIsNavbarOpen] = useState(false);

  const toggleNavbar = () => {
    setIsNavbarOpen(!isNavbarOpen);
  };

  return (
    <header className="header">
      <div className="container">
        <div className="header-title text-center">
          <h1>Karnala Charitable Trust, Pune</h1>
          <p>Established for Nation-building and Social Upliftment</p>
        </div>
        <nav className="navbar">
          <button className="navbar-toggler" type="button" onClick={toggleNavbar}>
            <span className="navbar-toggler-icon">&#9776;</span>
          </button>
          <div className={`navbar-collapse ${isNavbarOpen ? 'show' : ''}`}>
            <ul className="navbar-nav">
              <li className="nav-item">
                <Link className="nav-link" to="/" onClick={toggleNavbar}>Home</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/about" onClick={toggleNavbar}>About Us</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/projects" onClick={toggleNavbar}>Projects</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/events" onClick={toggleNavbar}>Events</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/publications" onClick={toggleNavbar}>Publications</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/contact" onClick={toggleNavbar}>Contact Us</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link btn-donate" to="/donations" onClick={toggleNavbar}>Donate</Link>
              </li>
            </ul>
          </div>
        </nav>
      </div>
    </header>
  );
};

export default Header;