import React from 'react';
import '../css/DonationsPage.css'; // Create or update the CSS file for custom styles
import qrCodeImage from '../images/img1/qr-code.png'; // Make sure to add the QR code image in the assets folder

const DonationsPage = () => {
    return (
        <div className="donations-page container mt-5">
            <h2>Support Our Cause</h2>
            <p>Your generous contributions help us to continue our mission of honoring freedom fighters and promoting social welfare. Heres how you can contribute:</p>
            
            <div className="donation-options">
                <div className="donation-card">
                    <h3>One-Time Donation</h3>
                    <p>Make a single contribution to support our ongoing projects and initiatives. No matter the amount, every bit helps!</p>
                    <button className="btn btn-donate">Donate Now</button>
                </div>
                
                <div className="donation-card">
                    <h3>Monthly Subscription</h3>
                    <p>Sign up for a monthly contribution to help us plan long-term projects and sustain our activities.</p>
                    <button className="btn btn-donate">Subscribe</button>
                </div>
                
                <div className="donation-card">
                    <h3>In-Kind Donations</h3>
                    <p>We accept donations of goods, services, or other resources that can assist in our operations.</p>
                    <button className="btn btn-donate">Inquire</button>
                </div>
            </div>
            
            <div className="payment-info mt-5">
                <h3>Bank Transfer Details</h3>
                <p>For direct bank transfers, please use the following details:</p>
                <ul>
                    <li><strong>Bank Name:</strong> The Cosmos Co-operative Bank Ltd</li>
                    <li><strong>Account Name:</strong> Karnala Charitable Trust</li>
                    <li><strong>Account Number:</strong> 01905010201872</li>
                    <li><strong>IFSC Code:</strong> COSB0000019</li>
                </ul>
                <p>For any assistance, please contact us at <a href="mailto:karnalact@gmail.com">karnalact@gmail.com</a>.</p>

            </div>
            <div className="qr-section mt-4">
                    <h4>Donate via QR Code</h4>
                    <p>Scan the QR code below to make a quick donation via your preferred UPI app:</p>
                    <img src={qrCodeImage} alt="QR Code for Donations" className="qr-code-img"/>
            </div>
        </div>
    );
};

export default DonationsPage;
