import React from 'react';
import { Container, Row, Col, Button, Card } from 'react-bootstrap';
import '../../src/css/HomePage.css'; // Ensure the correct path

// Root-relative paths to images in public folder
import aboutUsImage from '../../src/images/about-us.jpg';
import logoImage from '../images/logo.png'

const HomePage = () => {
  return (
    <div className="homepage">

      {/* Hero Section with Slideshow */}
      <section className="hero-section">
        <div className="slideshow-background">
          <div className="slideshow-overlay"></div>
          <div className="hero-content">

          <img src={logoImage} alt="About Us" className="logo-image"/>
            <h1>Welcome to Karnala Charitable Trust</h1>
            <p>
              Empowering communities through education, environmental initiatives, and social upliftment.
            </p>
            <Button variant="primary" href="#about" className="learn-more-btn">Learn More</Button>
          </div>
        </div>
      </section>

      {/* About Section */}
      <section id="about" className="about-section">
        <Container>
          <Row>
            <Col md={6}>
              <h2>About Us</h2>
              <p>
                Founded in 2000, Karnala Charitable Trust aims to promote patriotism and social welfare through various initiatives. Our founder, Chandrakant Shahasne, envisioned a platform to honor the sacrifices of Indian revolutionaries and educate the masses about their contributions.
              </p>
              <p>
                Our work spans across environmental conservation, health awareness, and social literacy, with a commitment to bringing meaningful change to communities.
              </p>
            </Col>
            <Col md={6}>
              <img src={aboutUsImage} alt="About Us" className="about-image" />
            </Col>
          </Row>
        </Container>
      </section>

      {/* Mission Section */}
      <section className="mission-section">
        <Container>
          <h2>Our Mission</h2>
          <Row>
            <Col md={4}>
              <Card className="mission-card">
                <Card.Body>
                  <Card.Title>Education</Card.Title>
                  <Card.Text>
                    Enhancing educational opportunities for youth and fostering a culture of learning.
                  </Card.Text>
                </Card.Body>
              </Card>
            </Col>
            <Col md={4}>
              <Card className="mission-card">
                <Card.Body>
                  <Card.Title>Environment</Card.Title>
                  <Card.Text>
                    Promoting environmental sustainability through various conservation projects.
                  </Card.Text>
                </Card.Body>
              </Card>
            </Col>
            <Col md={4}>
              <Card className="mission-card">
                <Card.Body>
                  <Card.Title>Social Welfare</Card.Title>
                  <Card.Text>
                    Supporting initiatives that address social issues and improve community well-being.
                  </Card.Text>
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </Container>
      </section>

      {/* Call-to-Action Section */}
      <section className="cta-section">
        <Container>
          <h2>Join Us in Making a Difference</h2>
          <p>
            Be a part of our journey towards a better tomorrow. Get involved in our initiatives, support our causes, and help us make a lasting impact.
          </p>
          <Button variant="primary" href="/contact">Get Involved</Button>
        </Container>
      </section>
    </div>
  );
};

export default HomePage;
