import React from 'react';
import '../css/EventsPage.css'; // Make sure to create or update the CSS file for custom styles

const EventsPage = () => {
  const upcomingEvents = [
    {
      title: 'Freedom Fighters Remembrance Program in Belgaum',
      date: '25th October 2024',
      description: 'A program dedicated to remembering and honoring freedom fighters in Belgaum.',
    },
    {
      title: 'Republic Day Parade and Exhibition at Shivaji Park, Mumbai',
      date: '26th January 2026',
      description: 'Join us for a grand parade and exhibition on Republic Day in Mumbai.',
    },
  ];

  const pastEvents = [
    {
      title: 'Revolutionary Photo Exhibition in Solapur',
      date: '14th September 2024',
      description: 'An exhibition showcasing the sacrifices of freedom fighters in Solapur.',
    },
    {
      title: 'National Patriotism Literary Conference, Pune',
      date: 'December 2022',
      description: 'A conference focusing on patriotism through literature held in Pune.',
    },
    {
      title: '948-Day Yatra Launch Event',
      date: '14th June 2023',
      description: 'The launch event for the 948-Day Yatra covering regions of Maharashtra and Goa.',
    },
  ];

  return (
    <div className="events-page container mt-5">
      <h2 className="text-center mb-4">Upcoming Events</h2>
      <div className="event-grid">
        {upcomingEvents.map((event, index) => (
          <div key={index} className="event-card">
            <div className="event-card-header">
              <h3 className="event-title">{event.title}</h3>
              <p className="event-date">{event.date}</p>
            </div>
            <p className="event-description">{event.description}</p>
          </div>
        ))}
      </div>

      {/* YouTube Video Section */}
      <div className="video-section text-center mt-5">
        <h2 className="mb-4">Watch Our Latest Event Highlights</h2>
        <iframe
          width="560"
          height="315"
          src="https://www.youtube.com/embed/Qpm5z9SPx1g?si=aZM5EFarmon9ekL3"
          title="YouTube video player"
          frameBorder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
          referrerPolicy="strict-origin-when-cross-origin"
          allowFullScreen
        ></iframe>
      </div>

      <h2 className="text-center mt-5 mb-4">Past Events</h2>
      <div className="event-grid">
        {pastEvents.map((event, index) => (
          <div key={index} className="event-card past-event-card">
            <div className="event-card-header">
              <h3 className="event-title">{event.title}</h3>
              <p className="event-date">{event.date}</p>
            </div>
            <p className="event-description">{event.description}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default EventsPage;
