// PublicationsPage.js
import React from 'react';
import { Link } from 'react-router-dom';
import { Container, Row, Col, Card, Button } from 'react-bootstrap';
import '../css/BooksGallery.css'; // Ensure this path is correct

const books = [
  { title: 'My Life Term', cover: '../Books/My-Life-Term-cover.jpg', file: '/Books/My-Life-Term.pdf' },
  { title: 'Adiwasi Krantikar', cover: '../Books/Adiwasi-Krantikar-cover.jpg', file: '/Books/Adiwasi-Krantikar.pdf' },
  { title: 'AseAahetSavarkar', cover: '../Books/AseAahetSavarkarCover.jpg', file: '/Books/AseAahetSavarkar.pdf' },
  { title: 'DindiKalyapanyachi', cover: '../Books/DindiKalyapanyachiCover.jpg', file: '/Books/DindiKalyapanyachi.pdf' },
  { title: 'Balidan', cover: '../Books/BalidanCover.jpg', file: '/Books/Balidan.pdf' },
  // Add other books as needed
];

const PublicationsPage = () => {
  return (
    <Container className="mt-5">
      <h2 className="text-center mb-4">Publications by Karnala Charitable Trust</h2>
      <Row>
        {books.map((book, index) => (
          <Col md={6} lg={4} xl={3} key={index} className="mb-4">
            <Card className="book-card">
              <Card.Img variant="top" src={book.cover} alt={book.title} className="book-cover" />
              <Card.Body>
                <Card.Title>{book.title}</Card.Title>
                <div className="d-flex justify-content-between">
                  <Link to={`/pdf-reader?file=${encodeURIComponent(book.file)}`} className="btn btn-primary">Read Online</Link>
                  <a href={book.file} download className="btn btn-secondary">Download PDF</a>
                </div>
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>
    </Container>
  );
};

export default PublicationsPage;
