import React from 'react';
import '../css/AboutPage.css'; // Make sure to create or update the CSS file for custom styles
import FounderImage from '../images/img1/founder.jpg';


const AboutPage = () => {
  return (
    <div className="about-page">
      <div className="container mt-5">
        <div className="row align-items-center">
          <div className="col-md-6">
            <h2>About Karnala Charitable Trust</h2>
            <p>
              Established in 2000 by Deshbhakt Koshkar Chandrakant Shahasane, Karnala Charitable Trust, Pune, is dedicated to preserving and spreading the legacy of Indian freedom fighters. Our mission is to promote national patriotism, environmental awareness, health literacy, and social upliftment through various activities and initiatives.
            </p>
            <p>
              We organize exhibitions featuring over 2,000 photos of freedom fighters across schools and institutions, aiming to educate and inspire the younger generation about the sacrifices made during India's independence movement.
            </p>
            <p>
              For more information, feel free to contact us at <a href="tel:+919881373585">+91 9881373585</a>.
            </p>
          </div>
          <div className="col-md-6">
            <img src={FounderImage} alt="Founder of Karnala Charitable Trust" className="img-fluid rounded shadow" />
          </div>
        </div>

        <div className="row mt-5">
          <div className="col-md-6 mb-4">
            <div className="card mission-card shadow-lg">
              <div className="card-body">
                <h3 className="card-title">Our Mission</h3>
                <ul className="card-list">
                  <li>Honor the sacrifices of freedom fighters</li>
                  <li>Promote environmental sustainability</li>
                  <li>Enhance health literacy in communities</li>
                  <li>Foster social upliftment through various initiatives</li>
                </ul>
              </div>
            </div>
          </div>
          <div className="col-md-6 mb-4">
            <div className="card contact-card shadow-lg">
              <div className="card-body">
                <h3 className="card-title">Contact Us</h3>
                <address>
                  <p>Address: 1234, Main Street, Pune, Maharashtra, India</p>
                  <p>Email: <a href="mailto:karnalact@gmail.com">karnalact@gmail.com</a></p>
                </address>
              </div>
            </div>
          </div>
        </div>

        <div className="history-section mt-5">
          <h3 className="section-title">Our History</h3>
          <ul className="history-timeline">
            <li>2000 - Trust established by Deshbhakt Koshkar Chandrakant Shahasane.</li>
            <li>2005 - First major exhibition organized in Pune.</li>
            <li>2010 - Expanded to include environmental awareness programs.</li>
            <li>2015 - Launched health literacy campaigns in rural areas.</li>
            <li>2020 - Celebrated 20 years of service with a national event.</li>
          </ul>
        </div>

        
        <div className="testimonials-section mt-5">
          <h3 className="section-title">What People Say</h3>
          <div className="testimonials">
            <blockquote>
              <p>"Karnala Charitable Trust has been instrumental in preserving our heritage and educating future generations. Their exhibitions are a testament to their dedication."</p>
              <footer>- Beneficiary Name</footer>
            </blockquote>
            <blockquote>
              <p>"The environmental and health initiatives of Karnala Charitable Trust have positively impacted many lives. Their commitment is truly commendable."</p>
              <footer>- Supporter Name</footer>
            </blockquote>
          </div>
        </div>

        
        <div className="impact-section mt-5">
          <h3 className="section-title">Our Impact</h3>
          <div className="impact-stats">
            <div className="stat">
              <h4>2,000+</h4>
              <p>Photos Exhibited</p>
            </div>
            <div className="stat">
              <h4>50+</h4>
              <p>Events Organized</p>
            </div>
            <div className="stat">
              <h4>1,000+</h4>
              <p>Beneficiaries Impacted</p>
            </div>
            <div className="stat">
              <h4>20+</h4>
              <p>Environmental Initiatives</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AboutPage;
